//配置请求基础路径
axios.defaults.baseURL = 'http://www.kangliuyong.com:10002';

//添加请求拦截器
axios.interceptors.request.use(config => {
  //在发送请求之前做些什么
  // 

  let appkey = 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=';

  if (config.method === 'get') {
    config.params = config.params || {};
    config.params.appkey = appkey;
  } else if (config.method === 'post') {
    config.data = config.data || {};
    //如果是post请求, 参数格式应为 key1=value1&key2=value2...
    // 
    let paramsString = '';
    for (let key in config.data) {
      paramsString += `${key}=${config.data[key]}&`
    }

    paramsString += `appkey=${appkey}`;

    config.data = paramsString;

    // 
  }
  

  //启动加载提示
  Toast.loading({
    message: '加载中...',
    forbidClick: true,
    //0: 加载提示不会自动关闭
    duration: 0
  });



  return config;
}, error => {
  // 对请求错误做些什么
  //关闭加载提示
  Toast.clear();
  return Promise.reject(error);
});

//添加响应拦截器
axios.interceptors.response.use(response => {
  // 对响应数据做点什么
  // 
  //关闭加载提示
  Toast.clear();
  return response;
}, error => {
  // 对响应错误做点什么
  //关闭加载提示
  Toast.clear();
  return Promise.reject(error);
});

Vue.use(VueAxios, axios)

// 定义全局过滤器 
// ('', 'yyyy-MM-dd hh:mm:ss') ==> 2021-10-20 12:20:30
// ('', 'yyyy/MM\dd hh:mm:ss') ==> 2021/10\20 12:20:30
// ('', 'yyyy/MM\dd') ==> 2021/10\20
// ('', 'hh:mm:ss') ==> 12:20:30

Vue.filter("formatDate",(value, format) =>{
    // value: 需要进行格式化的数据
    // format: 日期的格式 yyyy符号MM符号dd hh符号mm符号ss
    
    let currentDate = new Date(value);
    let year = currentDate.getFullYear();
    if(/(y+)/.test(format)){
      // 获取组匹配的内容
      let content = RegExp.$1;
      // 
      year = year.toString().slice(4 - content.length);
      format = format.replace(content, year);
    }
    // 

    let dateObject = {
      M: currentDate.getMonth() + 1,
      d: currentDate.getDate(),
      h: currentDate.getHours(),
      m: currentDate.getMinutes(),
      s: currentDate.getSeconds()
    };
    // 进行替换format
    for(let key in dateObject){
        // 根据key生成动态正则表达式
        let reg = new RegExp(`(${key}+)`);
        // 
        if(reg.test(format)){
          // 获取组的匹配内容
          let c = RegExp.$1;
          
          let v = dateObject[key] >= 10 ? dateObject[key] : c.length === 2 ? "0" + dateObject[key] : dateObject[key];
          format = format.replace(c,v);
          // 
        }
    }
    return format;
})
