import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

//修改state的唯一途径就是提交mutations
export default new Vuex.Store({
  state: {
    Nick:"kri",
    Desc:"这家伙很懒,没有留下东西!",
    Img:"http://www.kangliuyong.com:10002/assets/coffeeshop59720510371631751621918261998.jpeg"
  },
  mutations: {
    //改昵称
    changeNick(state,value){
      state.Nick = value;
    },
    // 改简介
    changeDesc(state,value){
      state.Desc = value;
    },
    // 该图片
    changeImg(state,value){
      state.Img = value;
    }
  },
  actions: {
  },
  modules: {
  },

})
